* Developed by Anthony Ngene
*/

/* @import "common/mixins"; */
@mixin google-font(\$font) {
    \$font: unquote(\$font);
    @import url(https://fonts.googleapis.com/css?family=#{\$font});
}

@mixin small-screen() {
    @media (max-width: 480px) {
        @content;
    }
}

@mixin medium-screen() {
    @media (max-width: 680px) {
        @content;
    }
}

@mixin large-screen() {
    @media (max-width: 960px) {
        @content;
    }
}

/* @import "common/variables"; */
\$text-color: #222;
\$body-color: #f6f4f1;
\$post-background: #d7ced7;
\$primary-color: #76323f;
\$primary-color-light: lighten(\$primary-color, 15%);
\$primary-color-dark: darken(\$primary-color, 15%);
\$secondary-color: #565656;
\$tertialy-color: #26a69a;
\$white: #fff;

\$text-font: "Lato", Arial, sans-serif;
\$header-font: "Montserrat", Georgia, serif;

\$text-size: 16px;
\$context-width: 960px;

@include google-font("Lato");
@include google-font("Montserrat");

* {
    margin: 0;
}

html,
body {
    height: 100%;
}

/* @import "extras"; */
.primary {
    background-color: \$primary-color;
}
.error {
    background-color: \$primary-color;
    padding-top: 12px;
    padding-bottom: 12px;
    margin-top: -10px;
}
.toast {
    background-color: \$primary-color;
}

h1,
h2,
h3 {
    font-family: \$header-font;
}

a {
    cursor: pointer;
}

select {
    background: transparent;
}

table tr {
    color: \$white;
}

strong {
    font-weight: bold;
}
label {
    max-width: 100px;
}

.new-document select {
    margin-bottom: 30px;
}

p,
span,
h1,
h2,
h3 {
    word-wrap: break-word;
}
.collection .collection-item {
    background-color: transparent;
    color: \$white;
}

.input-field label {
    top: 0.1rem;
}

.delete-btn {
    background-color: \$primary-color;
    margin-left: 15px;
    &:hover {
        background-color: darken(\$primary-color, 15%);
    }
}

.back-button {
    margin-top: 30px;
}

body {
    color: \$text-color;
    font-family: \$text-font;
    font-size: \$text-size;
}
#app {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-color: \$text-color;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: fixed;
    color: \$white;
}

// HEADER
nav {
    background-color: \$primary-color;

    .brand-logo {
        font-family: \$header-font;
    }

    .dropdown-content {
        margin-top: 64px;
        min-width: 140px;
        z-index: 999;

        li {
            a {
                color: \$primary-color;
                text-align: center;
            }
        }
        li .fa {
            margin-right: 0;
        }
    }

    #documents-dropdown {
        min-width: 200px;
    }
    @include small-screen() {
        li a {
            font-size: 13px;
        }
        i.right {
            display: none;
        }
    }
}

/* @import "content"; */

// MAIN
main {
    position: relative;
    .preloader-wrapper {
        position: fixed;
        top: 80px;
        left: 15px;
        z-index: 999;
    }
    .documents-div,
    .document-div,
    .form-div {
        padding: 60px 0;
        min-height: 85vh;
        background: rgba(0, 0, 0, 0.6);
    }
    .documents-div {
        position: relative;

        .edit-mode {
            display: inline-block;
            position: absolute;
            top: 20px;
            right: 30px;
        }
    }
    .fixed-action-btn {
        bottom: 100px;
        i,
        ul {
            color: \$white;
            .input-field,
            label {
                color: #fff;
            }
        }
    }
    .documents {
        position: relative;

        .card {
            position: relative;
            background-color: \$white;
            height: 250px;
            color: \$text-color;
            .view-document {
                display: block;
                height: 250px;
            }
            hr {
                margin-bottom: 15px;
            }
            .edit-btn,
            .delete-btn {
                position: absolute;
                top: -15px;
                background-color: \$primary-color;
            }
            .edit-btn {
                right: 50px;
            }
            .delete-btn {
                right: 0px;
            }
            .card-content {
                max-height: 250px;
                overflow: hidden;
                color: \$text-color;
            }

            .document-access {
                position: absolute;
                bottom: 5px;
                right: 5px;
                padding: 3px 10px;
                background-color: darken(\$white, 15%);
                border-radius: 5px;
                color: \$text-color;
            }
        }
        .recent-documents {
            text-align: center;
            font-size: 25px;
            margin: 0 0 10px 0;
        }
    }
    .document-div {
        .document {
            background-color: #fff;
            padding: 30px 25px;
            font-size: 20px;
            letter-spacing: 0.8px;
            word-spacing: 2px;
            color: \$text-color;
            border-radius: 10px;
            h1,
            h2,
            h3,
            h4 {
                margin-bottom: 3px;
            }
            .meta-info {
                margin-bottom: 20px;
                font-size: 16px;
                color: lighten(\$text-color, 15%);
            }
            img {
                max-width: 100%;
                margin: 10px 0;
            }
        }
    }

    .pagination {
        .waves-effect .material-icons {
            color: \$white;
        }
    }

    .select-role {
        width: 89%;
        display: block;
        margin-left: 45px;
    }

    .search-form {
        position: fixed;
        width: inherit;
        top: 53px;
        z-index: 980;

        input {
            background: rgba(0, 0, 0, 0.6);
            border-radius: 5px;
            padding-left: 15px;
        }

        label {
            margin-left: 15px;
        }

        button {
            margin-top: 5px;
        }
    }

    .edit-role,
    .edit-user {
        margin-right: 20px;
    }
}

#roleModal,
#userModal {
    .modal-content {
        background: \$text-color;
    }
}

.about-card {
    background: transparent;
    border: 1px \$white solid;
    border-radius: 10px;
}

.delete-modal {
    background-color: rgba(0, 0, 0, 0.6);
    .modal-footer {
        background: transparent;
        .red {
            margin-right: 10px;
        }
    }
}

// FOOTER
.page-footer {
    background-color: \$primary-color;
    padding: 10px 0px;
}

// nav control
.guest {
    .loggedIn,
    .forAdmin {
        display: none;
    }
}

.regularUser {
    .notLoggedIn,
    .forAdmin {
        display: none;
    }
}

.administrator {
    .notLoggedIn {
        display: none;
    }
}
