import React from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { login } from "../../actions/accessActions.js";
import { getDocuments } from "../../actions/documentActions.js";
import { validateLogin } from "../../utilities/validator.js";
import LoginForm from "./LoginForm.jsx";
import { handleError } from "../../utilities/errorHandler.js";

/**
 * LoginPage container
 *
 * @class LoginPage
 * @extends {React.Component}
 */
export class LoginPage extends React.Component {
  constructor(props) {
    super(props);

    this.state = { username: "", password: "", errors: {} };
    this.onSubmit = this.onSubmit.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  /**
   * Validate and submit the form
   *
   * @param {Object} event
   * @returns {Void} returns nothing
   */
  onSubmit(event) {
    event.preventDefault();
    const { valid, errors } = validateLogin(this.state);
    if (valid) {
      this.setState({ errors: {} });
      this.props
        .login(this.state)
        .then(() => {
          this.props.getDocuments().then(() => {
            this.context.router.push("/");
          });
        })
        .catch((error) => handleError(error));
    } else {
      this.setState({ errors });
    }
  }

  /**
   * Control input fields
   *
   * @param {Object} event
   * @returns {Void} returns nothing
   */
  onChange(event) {
    return this.setState({ [event.target.name]: event.target.value });
  }

  /**
   * Render the component
   *
   * @returns {Object} jsx component
   */
  render() {
    return (
      <LoginForm
        onSubmit={this.onSubmit}
        onChange={this.onChange}
        userDetails={this.state}
      />
    );
  }
}

LoginPage.propTypes = {
  login: PropTypes.func.isRequired,
  getDocuments: PropTypes.func.isRequired,
};

LoginPage.contextTypes = {
  router: PropTypes.object,
};

export default connect(null, { login, getDocuments })(LoginPage);
